@import "../../css/media.scss";

.info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  flex: 1;
  gap: 20px;

  @media #{$query-max-md} {
    flex-direction: row;
    align-items: center;
    min-height: 160px;

    .info__text {
      flex: 1;
      order: 1;
    }
    img {
      order: 2;
    }
  }

  .instructions {
    padding: 20px 20px 24px;

    border-radius: 8px;
    background: var(--K100);

    h3 {
      margin-bottom: 20px;
      font-size: 24px;
      font-weight: 700;
    }

    p:not(:last-child) {
      margin-bottom: 12px;
    }
  }

  img {
    width: 220px;
    height: auto;
    box-shadow: 1px 2px 6px rgba(0, 0, 0, 0.1), 4px 8px 20px rgba(0, 0, 0, 0.08);

    @media #{$query-min-md} {
      width: 220px;
    }
    @media #{$query-max-md} {
      width: 120px;
    }
  }

  h3 {
    margin-bottom: 10px;
    font-size: 28px;
    font-weight: 700;
  }

  ul {
    display: flex;
    flex-direction: column;
    gap: 6px;

    li {
      list-style-type: none;
      font-size: 16px;
      font-weight: 500;
      line-height: 1.2;

      &.capital {
        font-size: 18px;
        font-weight: 700;
      }
    }
  }
}
